<template>
	<!-- 隐患列表 -->
	<view class="content">
		<view class="task-box" v-if="troubleList.length > 0">
			<view v-for="item in troubleList" :key="item.eventId" class="task-item" style="margin-top: 16rpx;">
				<view class="topTitle">
					隐患编号 {{ item.eventId }}
					<uni-icons type="undo-filled" size="26" color="#409eff" style="float:right;" @click.stop="withdraw(item)" v-if="item.status=='2'"></uni-icons>
				</view>
				<view class="detail" @click="showTroubleDetail(item)">
					<view class="address">隐患详情：{{ item.eventDetail }}</view>
					<view class="address">隐患地点：{{ item.eventLocation }}</view>
				</view>
			</view>
		</view>
		<view class="empty-box" v-else>
			<image src="@/static/img/empty-image.png" class="empty-image"></image>
			<view class="empty-text">暂无数据</view>
		</view>
		<view class="flex flex-direction" style="padding:30rpx" v-if="roleId == '8' && !finish"><button class="cu-btn bg-blue lg" @click="report">隐患上报</button></view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			roleId: uni.getStorageSync('roleId'),
			troubleList: [],
			userId: '',
			id: '',
			finish: false
		};
	},
	onLoad(options) {
		console.log(options);
		this.userId = options.userId;
		this.id = options.id;
		this.finish = options.finish;
	},
	onShow() {
		if (this.userId) {
			this.getEventList(this.userId);
		} else {
			this.getFreeEventList();
		}
	},
	methods: {
		getEventList(userId) {
			taskAPI.getEventList({ userId }).then(res => {
				if (res.code == 200) {
					this.troubleList = res.data;
				}
			});
		},
		getFreeEventList() {
			//无任务的隐患列表
			taskAPI.getFreeEventList().then(res => {
				if (res.code == 200) {
					this.troubleList = res.data;
				}
			});
		},
		report() {
			//隐患上报
			uni.navigateTo({
				url: `../patrolTask/troubleReport?id=${this.id}`
			});
		},
		showTroubleDetail(item) {
			//查看隐患详情
			uni.navigateTo({
				url: `../patrolTask/troubleDetail?eventId=${item.eventId}`
			});
		},
		withdraw(item) {
			//撤回
			uni.showModal({
				content: '确定撤回该条记录吗？',
				success: rs => {
					if (rs.confirm) {
						let param = {
							eventId: item.eventId
						};
						taskAPI.logicDeleteEvent(param).then(res => {
							if (res.code == 200) {
								uni.showToast({
									icon: 'none',
									title: '已撤回',
									success: () => {
										this.getFreeEventList();
									}
								});
							}
						});
					}
				}
			});
		}
	}
};
</script>

<style scoped>
.task-box {
	margin-top: 10rpx;
}
.task-item {
	padding: 28rpx;
	margin-bottom: 22rpx;
	background-color: #fff;
	color: #333333;
}
.topTitle {
	padding-bottom: 30rpx;
	font-size: 32rpx;
	border-bottom: 1px solid #efefef;
}
.detail {
	line-height: 50rpx;
	padding: 20rpx 0;
}
.address {
	font-size: 28rpx;
	color: #999999;
	/* line-height: 50rpx; */
}
</style>
